import PageHeader from "./pageHeader";
import createContact from "./createContact";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { getAll } from "../contactsServices";
import ContactComponent from "./contactComponent";
const MyContacts = () => {
  const [contactsList, setContactsList] = useState([]);
  useEffect(() => {
    const getContacts = async () => {
      const contacts = await getAll();
      setContactsList(contacts.data);
    };

    getContacts();
  }, []);
  return (
    <>
      <PageHeader title={<h1>my contacts</h1>} />
      <h2>this is my contacts page</h2>
      <Link to={"/create-contact"}>add contact</Link>
      {contactsList.length ? (
        contactsList.map((contact) => {
          return (
            <ContactComponent
              key={contact.id}
              id={contact.id}
              contact={contact} //doesnt work this way. need to ask why
              name={contact.name}
              address={contact.address}
              number={contact.number}
            />
          );
        })
      ) : (
        <span className="m-5">loading your contacts...</span>
      )}

      {/* <button className="btn btn-danger m-2">edit</button>
            <button className="btn btn-danger">delete</button> */}
    </>
  );
};
export default MyContacts;
